.puzzle {
    position: absolute;
    left: 0;
    top: 0;
    width: 25%;
    height: 25%;
    transition: left .2s ease-in-out, top .2s ease-in-out;
    display: flex;
    justify-content: center;
    align-items: center;
    box-sizing: border-box;

    border: 1px solid #dee2e6;
    background-color: #f8f9fa;
    cursor: pointer; 
    z-index: 2;

    border-radius: .25rem;
}

.puzzle.puzzle-empty {
    background-color: white;
    border: 1px solid transparent;
    z-index: 1;
}

.puzzle.puzzle-done {
    color: #fd7200;
}

.puzzle-inner {
    font-size: 3rem;
    font-weight: 500;
}
