#winner {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    z-index: 10;
    background-color: rgb(0,0,0,0.5);
}

#winner span {
    color: #fd7200;
    font-size: 6rem;
    font-weight: 500;
}
