.puzzles-wrapper {
    position: relative;
    border: 1px solid black;
    padding: 2px;
    max-width: 500px;
    width: 100%;
    border-radius: .25rem;
}

.puzzles {
    position: relative;
    width: 100%;
    height: 100%;
}

.puzzles:before {
    content: '';
    display: block;
    padding-top: 100%;
}
