.status {
    position: relative;
    padding: 2px;
    max-width: 500px;
    width: 100%;    
    display: flex;
    justify-content: space-between;
}

.status-button{
    background-color: #f8f9fa;
    width: 25%;
    border-radius: .25rem;
}

.status-button:hover {
    background-color: #ffddc1;
    cursor: pointer;
    transition: background-color .3s ease-in-out;
}


.status-text{
    width: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
}